import preval from 'preval.macro';

export interface ICDDAJSONWithNameAndDescription {
  description: string;
  name: string;
  type: string;
}
export const cddaJSONWithNameAndDescription = preval`
const fs = require('fs');
  const path = require('path');
  module.exports = fs.readFileSync(path.join(__dirname, '..', 'data', 'translatedNameDesc.json'), 'utf-8');
` as string; // 数据集过大，会把webpack搞到内存不足，只能用字符串引入
